.big__corp {
  height: 100vh;
  width: 100%;
  @include flexCentered;
  flex-direction: column;

  &__content {
    // background-color: blue;
    height: 60vh;
    position: relative;
    z-index: 20;
    // @include flexCentered;
    // flex-direction: column;
    margin-top: 10em;
  }
  &-header {
    color: $color-heading;
    letter-spacing: 1.7px;
    font-size: 3rem;
    margin-bottom: 2em;
    margin-top: 10%;
  }

  &__logo-container {
    @include flexCentered;

    #google {
      color: #aeaeae;
      // border: 1px solid white;
      font-family: 'Poiret One', cursive;
      font-size: 3rem;
      font-weight: bolder;
      @media screen and (max-width: 550px) {
        font-size: 2rem;
      }
    }

    & .logos {
      // width: 12em;
      // height: 12em;
      // margin: 0 4em;
      // border: 1px solid white;
      width: 16%;
      padding: 0 1em;
    }

    #amazon {
      padding-top: 0.8em;
    }

    #heineken {
      // width: 13em;
      // height: 12em;
      padding-bottom: 0.8em;
    }
  }
  .background__circle-right {
    background: linear-gradient(
      180deg,
      rgba(76, 4, 88, 0.61) 17.65%,
      rgba(16, 60, 120, 0.81) 50.67%,
      rgba(0, 166, 175, 0.87) 88.13%
    );
    width: 50em;
    height: 50em;
    border-radius: 50%;
    position: absolute;
    margin-left: 26%;
    // transform: translateX(10em);
    top: 60%;
    text-align: center;
    z-index: 10;
    @include flexCentered;
    &-background {
      background: linear-gradient(
        rgba(5, 13, 33, 0),
        rgba(5, 13, 33),
        rgb(5, 13, 33)
      );
      width: 98%;
      height: 98%;
      border-radius: 50%;
    }

    @media screen and (max-width: 800px) {
      // margin-left: 20%;
      width: 40em;
      height: 40em;
      top: 62%;
    }
    @media screen and (max-width: 520px) {
      margin-left: -1%;
      width: 30em;
      height: 30em;
      top: 70%;
    }
    @media screen and (max-width: 380px) {
      margin-left: 10%;
      width: 20em;
      height: 20em;
      top: 70%;
    }
  }

  .background__circle-left {
    background: linear-gradient(
      100deg,
      rgba(113, 2, 133, 0.61),
      rgba(13, 78, 168, 0.81),
      rgba(0, 166, 175, 0.87)
    );
    width: 28em;
    height: 28em;
    border-radius: 50%;
    @include flexCentered;
    position: absolute;
    top: 140%;
    left: -15em;

    &-background {
      position: relative;
      background: linear-gradient(
        rgb(5, 13, 33),
        rgb(5, 13, 33),
        rgb(5, 13, 33)
      );
      width: 98%;
      height: 98%;
      border-radius: 50%;
    }
  }
  @media screen and (max-width: 600px) {
    &__content {
      height: 40vh;
    }
  }
  @media screen and (max-width: 360px) {
    &__content {
      height: 25vh;
    }
    &-header {
      font-size: 2rem;
      margin-top: 0;
    }

    .background__circle-right {
      width: 15em;
      height: 15em;

      // margin-left: 26%;
      // top: 57%;
    }
    .background__circle-left {
      width: 15em;
      height: 15em;

      left: -10em;
    }

    &-header {
      font-size: $fontSize-Sm;
    }

    #google {
      font-size: $fontSize-SS;
    }
  }
}
